.Polygon-LineTo {
  -webkit-box-shadow: 0px 1px 1px 0 white;
          box-shadow: 0px 1px 1px 0 white;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: -webkit-box-shadow 0.21s ease-in-out;
  transition: -webkit-box-shadow 0.21s ease-in-out;
  -o-transition: box-shadow 0.21s ease-in-out;
  transition: box-shadow 0.21s ease-in-out;
  transition: box-shadow 0.21s ease-in-out, -webkit-box-shadow 0.21s ease-in-out;
  z-index: -1;
}

